<template>
  <div class="id-photo-editor">
    <!--  -->
    <ad-top></ad-top>
    <!--  -->
    <el-row type="flex" justify="center">
      <el-col :md="20">
        <div class="intro-detail">
          {{ $t('tools.idPhotoEditor.introduction') }}
          <el-link type="primary" href="" target="_blank">Learn more!</el-link>
        </div>
      </el-col>
    </el-row>
    <el-row>
        <el-col :md="12">
            <img id="id-photo-img" ref="idPhotoImg">
        </el-col>
    </el-row>
    <!--  -->
    <ad-bottom></ad-bottom>
  </div>
</template>

<script>
import adTop from '../../components/ad/ad-top'
import adBottom from '../../components/ad/ad-bottom'
import Cropper from 'cropperjs'

export default {
    name: "id-photo-editor",
    data() {
        return {
            cropper: new Object()
        }
    },
    components: {
        adTop,
        adBottom
    },
    methods: {
        initCropper() {
            const cropper = new Cropper(this.$refs.idPhotoImg, {
                aspectRatio: 16 / 9
            });
            this.cropper = cropper;
        }
    },
    mounted() {
        this.initCropper();
    }
}
</script>

<style scoped>
#id-photo-img {
  display: block;
  max-width: 100%;
}
</style>